<template>
  <div class="card">
    <div class="card-header pb-0">
      <h6>Tambah Berita</h6>
    </div>
    <div class="card-body px-0 pt-0 pb-2">
      <div class="table-responsive p-0">
        <table class="table align-items-center mb-0">
          <div class="card-body">
            <div class="col-md-auto">
              <label for="judul" class="form-control-label">Judul Berita</label>
              <input class="form-control" type="text" v-model="judul" id="judul" />
            </div>
            <div class="col-md-auto mt-2">
              <label for="deskripsi" class="form-control-label">Deskripsi</label>
              <input class="form-control" type="text" v-model="deskripsi" id="deskripsi" />
            </div>
            <div class="col-md-auto mt-2">
              <label for="fileInput" class="form-control-label">Photo/Video</label>
              <input class="form-control" type="file" id="fileInput" accept="image/*,video/*" ref="fileInput" @change="previewImage" />
              <p class="text-xs text-muted mt-2" v-if="fileName">{{ fileName }}</p>
            </div>
          </div>
        </table>
        <div class="col-6 text-home px-4 pb-4 pt-0 mt-2">
          <argon-button color="dark" variant="gradient" @click="tambahBerita">
            <i class="fas fa-plus me-2"></i>
            Tambah Berita
          </argon-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ArgonButton from "@/components/ArgonButton.vue";
import axios from 'axios';

export default {
  name: "TambahBerita",
  components: {
    ArgonButton,
  },
  data() {
    return {
      judul: '',
      deskripsi: '',
      file: null,
      fileName: '',
    };
  },
  methods: {
    previewImage() {
      this.fileName = this.$refs.fileInput.files[0].name;
      this.file = this.$refs.fileInput.files[0];
    },
   
    tambahBerita() {
      const formData = new FormData();
      formData.append('judul', this.judul);
      formData.append('deskripsi', this.deskripsi);
      formData.append('image', this.file);

      axios.post('http://127.0.0.1:8000/api/beritas', formData)
        .then(response => {
          console.log(response.data);
          this.$router.push('/berita');
        })
        .catch(error => {
          console.log(error);
        });
    },
  },
};
</script>
